import React from "react";

import {Link} from "react-router-dom";

import logoIMG from "../../assets/images/LOGO2.png";
import ladyIMG from "../../assets/images/lady-justice.png";
import gerarRPV from "../../assets/images/icons/gerar-rpv-icon.svg";
import buscarRPV from "../../assets/images/icons/buscar-rpv-icon.svg";

import './style.css'

function Landing() {
  return (
    <div id="page-landing">
      <div id="page-landing-content" className="container">
        <div className="logo-container">
          <img src={logoIMG} alt="silva e freitas" />
        </div>
        <div className="lady-container">
          <img src={ladyIMG} alt="dama da justiça" className="lady-image"/>
        </div>
        <div className="buttons-container">
          <Link to="/rpv-generate" className="gerarRPV">
            <img id="gerarRPV" src={gerarRPV} alt="dama da justiça" className="iconbuttom"/>
            GERAR RPV
          </Link>
          <Link to="/rpv-search" className="buscarRPV">
            <img id="BuscarRPV" src={buscarRPV} alt="dama da justiça" className="iconbuttom"/>
            BUSCAR RPV
          </Link>
        </div>
      </div>
    </div>
  );
}
export default Landing;