import React from "react";
import { BrowserRouter, Route} from "react-router-dom"
import Landing from "./pages/Landing";
import RPVGenerator from "./pages/RPVGenerator";
import RPVSearch from "./pages/RPVSearch";

function Routes() {
  return (
    <BrowserRouter>
      <Route path="/" exact component={Landing} />
      <Route path="/rpv-generate" component={RPVGenerator} />
      <Route path="/rpv-search" component={RPVSearch} />
    </BrowserRouter>
  )
}

export default Routes;