import React, { FormEvent, useState } from "react";
import Input from "../../components/input";
import Report from "../../components/report";
import NumberFormat from "react-number-format";
import { Divider, Steps } from 'rsuite';
import 'rsuite/dist/styles/rsuite-default.css';

import warning from "../../assets/images/icons/warning.svg";
import PageHeader from "../../components/PageHeader";

import "./styles.css"
import Select from "../../components/Select";



function RPVSearch() {
  const [page, setPage] = useState(1);
  const [date, setDate] = useState(getDate);
  const [surveydate, setSurveyDate] = useState('');
  const [typeBenefit, setTypeBenefit] = useState('');
  const [percentBenefit, setPercentBenefit] = useState('');
  const [lawyerName, setLawyerName] = useState('');
  const [clientName, setClientName] = useState('');
  const [clientCPF, setClientCPF] = useState('');
  const [courtroom, setCourtroom] = useState('');
  const [processNumber, setProcessNumber] = useState('');
  const [totalValue, setTotalvalue] = useState('');
  const [valuePercentFees, setValuePercentFees] = useState('');
  const [qtdBenefits, setQtdBenefits] = useState('');
  const [valueBenefits, setValueBenefits] = useState('');
  const [initialFees, setInitialFees] = useState('0,00');
  const [discountValue, setDiscountValue] = useState('0');
  const [discountType, setDiscountType] = useState('');
  const [totalBenefits, setTotalBenefits] = useState('');



  function getFloatMoney(value: string) {
    value = value.replace('.', '')
    value = value.replace(',', '.')
    //value = Number.parseFloat(value);
    return Number.parseFloat(value).toFixed(2);
  }
  function currency(float: string) {
    let value = float.toString();
    value = value.replace(/\D/g, "");
    value = value.replace(/(\d)(\d{2})$/, "$1,$2");
    value = value.replace(/(?=(\d{3})+(\D))\B/g, ".");
    //console.log(value)
    return value;
  }
  
  function getDate() {
    var today = new Date();

    return today.getFullYear() + '-' + ('0' + (today.getMonth() + 1)).slice(-2) + '-' + ('0' + today.getDate()).slice(-2);
  }
  function discountValueReal(value: string, type: string) {
    let valor = getFloatMoney(value)
    if (parseFloat(value) == 0)
      return 0
    if (type === "R$")
      return Number.parseFloat(valor).toFixed(2);
    else {
      let valorra = (10 * ((parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits))) +
        ((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))))) / 100
      //let tipo = ((parseFloat(discountValue) / 100) * parseFloat(getFloatMoney(totalValue))).toFixed(2)
      return Number.parseFloat(valorra.toString()).toFixed(2);
    }
  }
  function getTotalBenefits (percentBenef: string, qtdBenef: string, valueBenef: string, typeBenef:string){
    if(typeBenef=="%")
      return Number.parseFloat(((parseFloat(qtdBenef) * parseFloat(getFloatMoney(valueBenef)))*parseFloat(percentBenef)/100).toFixed(2)).toFixed(2)
    if(typeBenef=="R$")
    return Number.parseFloat(((parseFloat(qtdBenef) * parseFloat(getFloatMoney(valueBenef)))).toFixed(2)).toFixed(2)
  }
  function createRPV(e: FormEvent) {
    e.preventDefault()

    Report({
      initialFees: getFloatMoney(initialFees),
      lawyerName,
      clientName,
      clientCPF,
      courtroom,
      processNumber,
      totalValue: getFloatMoney(totalValue),
      valuePercentFees,
      qtdBenefits,
      valueBenefits: getFloatMoney(valueBenefits),
      discountValue: discountValueReal(discountValue, discountType),
      date,
      typeBenefit,
      percentBenefit,
      surveydate,
      totalBenefits: getTotalBenefits(percentBenefit, qtdBenefits, valueBenefits, typeBenefit)
    });
  }
  function getData(data: string) {
    var ano = data.split('-')[0];
    var mes = data.split('-')[1];
    let dia = data.split('-')[2];
    return `${dia}/${mes}/${ano}`
  }
  function OnBoardingFive() {
    return (
      <div className="step5">
        <fieldset>
          <legend>Confirme as informações</legend>
          <div className="fild">
            <label>Data:</label><label className="result">{getData(date)}</label>
          </div>
          <div className="fild">
            <label>Advogado:</label><label className="result">{lawyerName}</label>
          </div>
          <div className="fild">
            <label>Cliente:</label><label className="result">{clientName}</label>
          </div>
          <div className="fild">
            <label>CPF do cliente:</label><label className="result">{clientCPF}</label>
          </div>
          <div className="fild">
            <label>Vara do processo:</label><label className="result">{courtroom}</label>
          </div>
          <div className="fild">
            <label>Numero do processo:</label><label className="result">{processNumber}</label>
          </div>
          <div className="fild">
            <label>Valor da RPV:</label><label className="result">R$ {totalValue}</label>
          </div>
          <div className="fild">
            <label>Data do levantamento:</label><label className="result">{getData(surveydate)}</label>
          </div>
          <div className="fild">
            <label>Honorários contratados:</label>
            <label className="result">
              {valuePercentFees}% = R${currency(((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))).toFixed(2))}
            </label>
          </div>
          <div className="fild">
            <label>Benefícios:</label>
            <label className="result">
              {typeBenefit==='%' && <p>
                {percentBenefit}%  ({qtdBenefits} x R$ {valueBenefits}) 
                = R$ {currency(((parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits)))*parseFloat(percentBenefit)/100).toFixed(2))}
              </p>}
              

              {typeBenefit==='R$' && <p>
                {qtdBenefits} x R$ {valueBenefits} 
                = R$ {currency(((parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits)))).toFixed(2))}
              </p>}
               
            </label>
          </div>
          <div className="fild">
            <label>Honorários advocatícios iniciais:</label>
            <label className="result">R$
              {currency(parseFloat(initialFees).toFixed(2))}
            </label>
          </div>
          <div className="fild">
            <label>Honorários advocatícios finais:</label>
            <label className="result">R$ 
              {currency(((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))).toFixed(2))} +
              {typeBenefit==='R$' && <p>
                {currency(((parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits)))).toFixed(2))}
              </p>}
              {typeBenefit==='%' && <p>
                {currency(((parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits)))*parseFloat(percentBenefit)/100).toFixed(2))}
              </p>}
              

            </label>
          </div>
          <div className="fild">
            <label>Descontos:</label><label className="result">
              {discountType === "%" && `${discountValue}% = `}
              R$
              {currency(discountValueReal(discountValue, discountType).toString())}
            </label>
          </div>
          <div className="fild">
            <label>Saldo final dos honorários:</label><label className="result">R$
              {typeBenefit == "R$" &&
                currency(((parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits))) +
                  ((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))) -
                  parseFloat(discountValueReal(discountValue, discountType).toString())).toFixed(2)
                  ) 
              }
              {typeBenefit == "%" &&
                currency((((parseFloat(qtdBenefits) * parseFloat(percentBenefit)* parseFloat(getFloatMoney(valueBenefits))/100)) +
                  ((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))) -
                  parseFloat(discountValueReal(discountValue, discountType).toString())).toFixed(2)
                  ) 
              }
            </label>
          </div>
          <div className="fild">
            <label>Saldo final da RPV: </label><label className="result">R$
              {
                typeBenefit == "R$" &&
                currency((parseFloat(getFloatMoney(totalValue)) -
                  (parseFloat(qtdBenefits) * parseFloat(getFloatMoney(valueBenefits))) -
                  ((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))) +
                  parseFloat(discountValueReal(discountValue, discountType).toString())).toFixed(2))
              }
              {
                typeBenefit == "%" &&
                currency((parseFloat(getFloatMoney(totalValue)) -
                  ((parseFloat(qtdBenefits) * parseFloat(percentBenefit)* parseFloat(getFloatMoney(valueBenefits))/100)) -
                  ((parseFloat(valuePercentFees) / 100) * parseFloat(getFloatMoney(totalValue))) +
                  parseFloat(discountValueReal(discountValue, discountType).toString())).toFixed(2))
              }
            </label>
          </div>


        </fieldset>
      </div>
    )
  }
  function goNextStep() {
    if (page === 6) return;
    setPage(page => page + 1)
  }
  function goBeforeStep() {
    if (page === 1) return;
    setPage(page => page - 1)
  }
  
  return (
    <div className="page-rpv-generate">
      <PageHeader
        title="Gerar novo RPV"
        description="O primeiro passo é preencher o formulario de RPV abaixo."
      />

      <form onSubmit={createRPV}>
        <main>
          <div>
            <div id="step-bar-progress">
              <Steps current={page - 1}>
                <Steps.Item />
                <Steps.Item />
                <Steps.Item />
                <Steps.Item />
                <Steps.Item />
                <Steps.Item />
              </Steps>
            </div>

            {page === 1 && <div className="step1">
              <fieldset>
                <legend>Data do acerto</legend>
                <Input type="date" name="date" label="Data do acerto" value={date} onChange={(e) => { setDate(e.target.value) }} />
              </fieldset>
              <fieldset>
                <legend>Advogado</legend>
                <Select
                  name="lawyerName"
                  defaultOption="Selecione um advogado"
                  value={lawyerName}
                  onChange={(e) => { setLawyerName(e.target.value) }}
                  options={[
                    { value: 'Dr. Mateus Augusto Silva Amaral', label: "Dr.  Mateus Augusto Silva Amaral" },
                    { value: 'Dr. Fillipe André Souza Freitas', label: "Dr.  Fillipe André Souza Freitas" },
                    { value: 'Dra. Cristiane Fonseca da Silva', label: "Dra. Cristiane Fonseca da Silva" },
                    { value: 'Dr. Filipe Meira Martins', label: "Dr.  Filipe Meira Martins" },
                    { value: 'Dra. Mariana Oliveira Lafetá', label: "Dra. Mariana Oliveira Lafetá" },
                    { value: 'Dra. Maria Clara Souza', label: "Dra. Maria Clara Souza" },
                    { value: 'Dra. Ana Paula Sousa', label: "Dra. Ana Paula Sousa" },
                  ]}
                />
              </fieldset>

              <fieldset>
                <legend>Dados do cliente</legend>
                <Input name="clientName" label="Nome" value={clientName} onChange={(e) => { setClientName(e.target.value) }} />
                <div className="input-block">
                  <label htmlFor="processNumber">CPF</label>
                  <NumberFormat format="###.###.###-##" name="clientCPF" label="CPF" value={clientCPF} onChange={(e) => { setClientCPF(e.target.value) }} />
                </div>
              </fieldset>
            </div>}

            {page === 2 && <div className="step2">
              <fieldset>
                <legend>Dados do processo</legend>

                <Select
                  name="courtroom"
                  label="Vara do processo"
                  value={courtroom}
                  onChange={(e) => { setCourtroom(e.target.value) }}
                  options={[
                    { value: 'PJe-MG 1º Grau', label: 'PJe-MG 1º Grau' },
                    { value: 'PJe-MG 2º Grau', label: 'PJe-MG 2º Grau' },
                    { value: 'PJe-MG 3º Grau', label: 'PJe-MG 3º Grau' },
                    { value: '1ª Vara do Juizado Especial Federal de Montes Claros/MG', label: '1ª Vara do Juizado Especial Federal de Montes Claros/MG' },
                    { value: '2ª Vara do Juizado Especial Federal de Montes Claros/MG', label: '2ª Vara do Juizado Especial Federal de Montes Claros/MG' },
                    { value: '3ª Vara do Juizado Especial Federal de Montes Claros/MG', label: '3ª Vara do Juizado Especial Federal de Montes Claros/MG' },
                  ]}
                />
                <div className="input-block">
                  <label htmlFor="processNumber">Numero do processo</label>
                  <NumberFormat format="#######-##.####.#.##.####" name="processNumber" label="Numero do processo" value={processNumber} onChange={(e) => { setProcessNumber(e.target.value) }} />
                </div>

              </fieldset>
            </div>}

            {page === 3 && <div className="step3">
              <fieldset>
                <legend>Valor da RPV</legend>
                <div className="input-block">
                  <label htmlFor="totalValue">Valor total da RPV</label>
                  <div className="input-currency">
                    <input id="prefix" value="R$" disabled />
                    <NumberFormat
                      className="input-value"
                      decimalSeparator=','
                      thousandSeparator="."
                      name="totalvalue"
                      label="Valor Total"
                      value={totalValue}
                      onChange={(e) => { setTotalvalue(e.target.value) }}

                    />
                  </div>
                </div>
                <Input type="date" name="surveyDate" label="Data do levanatamento" value={surveydate} onChange={(e) => { setSurveyDate(e.target.value) }} />

              </fieldset>

            </div>}
            {page === 4 && <div className="step4">

              <fieldset>
                <legend>Honorarios</legend>
                <div className="block-fees">
                  <Input name="descriptionFees" label="descrição" value="Porcentagem dos honorários advocatícios" disabled />
                  <div className="input-block">
                    <label htmlFor="valueBenefits">Valor em %</label>
                    <div className="input-currency">
                      <input id="prefix" value="%" disabled />
                      <NumberFormat isNumericString={true} className="input-value" name="valuePercentFees" value={valuePercentFees} onChange={(e) => { setValuePercentFees(e.target.value) }} />
                    </div>
                  </div>


                </div>

                <div className="initial-fees">
                  <Input name="initialFees" label="descrição" value="honorários advocatícios iniciais" disabled />
                  <div className="input-block">
                    <label htmlFor="valueBenefits">Valor em R$</label>
                    <div className="input-currency">
                      <input id="prefix" value="R$" disabled />
                      <NumberFormat
                        className="input-value"
                        decimalSeparator=','
                        thousandSeparator="."
                        decimalScale={2}
                        name="valueInitialFees"
                        label="Valor unitario"
                        value={initialFees}
                        onChange={(e) => { setInitialFees(e.target.value) }}
                      />
                    </div>

                  </div>
                </div>
              </fieldset>
              <fieldset>
                <legend>Benefícios</legend>
                <Select
                  id="selectTypeDiscount"
                  name="discountType"
                  label="Selecione o tipo de benefícios"
                  value={typeBenefit}
                  onChange={(e) => { setTypeBenefit(e.target.value) }}
                  options={[
                    { value: "R$", label: 'Benefício por quantidade' },
                    { value: "%", label: 'Benefício por de parcelas vicendas' },
                  ]}
                />
                {typeBenefit === "%" &&
                  <div className="block-benefits-percent">
                    <Input name="parcentBenefits" label="% do benefício" value={percentBenefit} onChange={(e) => { setPercentBenefit(e.target.value) }}/>
                    <Input name="qtdBenefits" label="Quant. parcelas vicendas" value={qtdBenefits} onChange={(e) => { setQtdBenefits(e.target.value) }} />
                    <div className="input-block">
                      <label htmlFor="valueBenefits">Valor unitário da parcela</label>
                      <div className="input-currency">
                        <input id="prefix" value="R$" disabled />
                        <NumberFormat
                          className="input-value"
                          decimalSeparator=','
                          thousandSeparator="."
                          name="valueBenefits"
                          label="Valor unitario"
                          value={valueBenefits}
                          onChange={(e) => { setValueBenefits(e.target.value) }}
                        />
                      </div>

                    </div>
                  </div>
                }

                {typeBenefit === "R$" &&
                  <div id="block-benefits">
                  <Input name="qtdBenefits" label="Quantidade de benefícios" value={qtdBenefits} onChange={(e) => { setQtdBenefits(e.target.value) }} />
                  <div className="input-block">
                    <label htmlFor="valueBenefits">Valor unitário do benefício</label>
                    <div className="input-currency">
                      <input id="prefix" value="R$" disabled />
                      <NumberFormat
                        className="input-value"
                        decimalSeparator=','
                        thousandSeparator="."
                        name="valueBenefits"
                        label="Valor unitario"
                        value={valueBenefits}
                        onChange={(e) => { setValueBenefits(e.target.value) }}
                      />
                    </div>
                  </div>
                </div>
                }

                
              </fieldset>
            </div>}
            {page === 5 && <div className="step4">
              <fieldset>
                <legend>
                  Descontos
                </legend>

                <Select
                  id="selectTypeDiscount"
                  name="discountType"
                  label="O desconto é em R$(reais) ou %(porcentagem)"
                  value={discountType}
                  onChange={(e) => { setDiscountType(e.target.value) }}
                  options={[
                    { value: "R$", label: 'R$' },
                    { value: "%", label: '%' },
                  ]}
                />
                <div className="input-block">
                  <label>Valor</label>
                  <div className="input-currency">
                    <NumberFormat
                      decimalSeparator={","}
                      thousandSeparator={"."}
                      decimalScale={2}
                      name="discountValue"
                      label="valor"
                      value={discountValue}
                      onChange={(e) => { setDiscountValue(e.target.value) }}
                    />
                  </div>
                </div>


              </fieldset>
            </div>}
            {page === 6 && <OnBoardingFive />}


          </div>

          <footer>
            {page === 1 && <p><img src={warning} alt="aviso importante!" />Importante! <br />Preencha todos os campos</p>}
            {page !== 1 && <button onClick={goBeforeStep} type="button" id="btn-next">Voltar</button>}
            {page !== 6 && <button onClick={goNextStep} type="button" id="btn-next">Proximo</button>}
            {page === 6 && <button type="submit">Gerar RPV</button>}

          </footer>
          <div className="warn">
            {page !== 1 && <p><img src={warning} alt="aviso importante!" />Importante! <br />Preencha todos os campos</p>}
          </div>

        </main>
      </form>
    </div>
  )
}

export default RPVSearch;