//import React from "react";
import pdfmake from "pdfmake/build/pdfmake"
import pdfFonts from "pdfmake/build/vfs_fonts"
import extenso from "extenso"

var dia_informado ;
var mes_informado ;
var ano_informado ;
function getMes(mes){
  let mess
  switch (mes){
    case 1: return "Janeiro";     break;
    case 2: mess = "Fevereiro"; break;
    case 3: mess = "Março";     break;
    case 4: mess = "Abril";     break;
    case 5: mess = "Maio";        break;
    case 6: mess = "Junho";     break;
    case 7: mess = "Julho";     break;
    case 8: mess = "Agosto";     break;
    case 9: mess = "Setembro";     break;
    case 10: mess = "Outubro";     break;
    case 11: mess = "Novembro"; break;
    case 12: mess = "Dezembro"; break;
  }
  return mess
}
function getDateExtense(date){
  ano_informado = date.split('-')[0];
  mes_informado = date.split('-')[1];
  dia_informado = date.split('-')[2];
  return dia_informado + " de " + getMes(parseInt(mes_informado)) + " de " + ano_informado 
}
function getDate(date){
  ano_informado = date.split('-')[0];
  mes_informado = date.split('-')[1];
  dia_informado = date.split('-')[2];
  return dia_informado + "/" + mes_informado+ "/" + ano_informado 
}
function currency(float) {
  let value = float.toString();
  value = value.replace(/\D/g, "");
  value = value.replace(/(\d)(\d{2})$/, "$1,$2");
  value = value.replace(/(?=(\d{3})+(\D))\B/g, ".");
  //console.log(value)
  return value;
}

function Report(
  { lawyerName,
    clientName,
    clientCPF,
    courtroom,
    processNumber,
    totalValue,
    valuePercentFees,
    qtdBenefits,
    valueBenefits,
    initialFees,
    discountValue,
    date,
    typeBenefit,
    percentBenefit,
    surveydate,
    totalBenefits
  }) {
    const descontoline =  [
      { text: 'Descontos',height:[50]  },
      {
        text: `- R$  ${currency(discountValue)} `,
        bold: true, alignment: 'justify'
      }
    ]

    const feesContractQTD =[
      { text: '\nHonorários contratados'},
      { lineHeight: 1.5, text: `${valuePercentFees}%    referente    aos    valores    atrasados    +    ${qtdBenefits}   benefícios    (art.    13    tabela    de    honorários    OAB/MG)`, bold: true, alignment: 'justify' }
    ]
    const feesContractPercent =[
      { text: '\nHonorários contratados'},
      { lineHeight: 1.5, text: `${valuePercentFees}%    referente    aos    valores    atrasados    +    ${percentBenefit}%   de    ${qtdBenefits}    parcelas    vicendas   (art.    13    tabela    de    honorários    OAB/MG)`, bold: true, alignment: 'justify' }
    ]
    const table1 = [
      ['HONORÁRIOS', '']
      ,
      
      [
        { text: 'Honorários advocaticios iniciais' }, { text: `  R$ ${initialFees}`, bold: true, alignment: 'justify' }
      ],
      [
        { text: 'Honorários advocaticios finais',height:[50]  },
        {
          text: `  R$  ${currency(((totalValue * valuePercentFees) / 100).toFixed(2))} + ${currency(totalBenefits)}`,
          bold: true, alignment: 'justify'
        }
      ],
      [
        { text: 'Saldo final' },
        { text: `  R$ ${currency(((((totalValue * valuePercentFees) / 100)) + parseFloat(totalBenefits) - discountValue).toFixed(2))}`, bold: true, alignment: 'justify' }
      ],
    ]

    typeBenefit == "%" && table1.splice(1,0,feesContractPercent);
    typeBenefit == "R$" && table1.splice(1,0,feesContractQTD);
    discountValue != 0 && table1.splice(4,0,descontoline);
  pdfmake.vfs = pdfFonts.pdfMake.vfs
  
  const details = [
    {
      text: 'ADVOCACIA',
      fontSize: 25,
      bold: true,
      margin: [20, 0, 0, 0]
    },
    {
      text: 'Mateus Augusto Silva – OAB/MG 119.571\n Fillipe André Souza Freitas – OAB/MG 119.584\n',
      alignment: 'right',
      margin: [0, -13, 0, 0]
    },
    '_______________________________________________________________________________________________\n\n',
    {
      text: 'R E C I B O – PRESTAÇÃO DE CONTAS\n',
      alignment: 'center',
      fontSize: 14,
      bold: true
    },
    {
      text: '\nDADOS DO CLIENTE:',
      bold: true,
      decoration: 'underline'
    },
    `NOME:  ${clientName.toUpperCase()}`,
    `CPF:  ${clientCPF}`,
    {
      text: '\nDADOS DO PROCESSO:',
      bold: true,
      decoration: 'underline'
    },
    `${courtroom}`,
    {
      text: [
        { text: '\nProcesso número:', bold: true },
        `  ${processNumber}`
      ],
    },
    {
      text: '\nREFERENTE:',
      bold: true,
      decoration: 'underline'
    },
    "Prestação de serviços advocatícios referentes à atuação em processo previdenciário que figura como parte contrária o Instituto Nacional do Seguro Social – INSS.\n\n",
    {
      table: {
        widths: ["40%", "60%"],
        body: table1
      },
    },
    '\n\n',
    {
      table: {
        widths: ["40%", "20%", "20%", "20%"],
        body: [
          [
            {
              text: 'CRÉDITOS', bold: true
            },
            { text: 'Crédito', bold: true },
            { text: 'Débito', bold: true },
            { text: 'Saldo', bold: true }],
          [
            `Valor levantado (RPV) data ${getDate(surveydate)}`,
            { text: `R$  ${currency(totalValue)}`, bold: true },
            { text: `R$  ${currency(((((totalValue * valuePercentFees) / 100)) + parseFloat(totalBenefits) - discountValue).toFixed(2))}`, bold: true },
            { text: `R$  ${currency(
              (((parseFloat(totalValue) + parseFloat(discountValue)) - ((totalValue * valuePercentFees) / 100)) - (totalBenefits)).toFixed(2)
              )}`, bold: true }
          ],
        ]
      },
    },
    '\n\n',
    {
      alignment:'justify',
      text:[`Declaro, que no dia ${getDate(date)}, recebi o valor de `,{bold: true, text:`R$ ${currency(
        (((parseFloat(totalValue) + parseFloat(discountValue)) - ((totalValue * valuePercentFees) / 100)) - (totalBenefits)).toFixed(2)
        )}`},` (${extenso(currency((((parseFloat(totalValue) + parseFloat(discountValue)) - ((totalValue * valuePercentFees) / 100)) - (totalBenefits)).toFixed(2)), { mode: 'currency' })}), referente ao valor líquido residual levantado no processo previdenciário acima citado, pagos pelo ${lawyerName}, através de cheque nominal, pelo que, dou plena e geral quitação em relação aos valores supra mencionados.`]},
    '\n',
    { text: `Montes Claros/MG, ${getDateExtense(date)}.`, alignment: 'center' },
    '\n\n\n\n',
    {
      alignment: 'center',
      fontSize:9.5,
      columns: [
        {
          text: `____________________________________\n ${clientName}\n${clientCPF}`
        },
        {
          text: `____________________________________\n SILVA E FREITAS\nSOCIEDADE DE ADVOGADOS\nOABMG  3.818 `
        },
      ]
    }
  ]



  const docDefinitions = {
    pageSize: 'A4',
    fontSize:9,
    PageMargins: [10, 10, 10, 10],
    content: [details],
  }

  pdfmake.createPdf(docDefinitions).download()
}

export default Report;
